
.menu-list {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  display: inline-block;
  position: relative;
}
.menu-list__item  {
  display: inline-block;
  border: none;
}

.menu-list__anchor {
  display: inline-block;
  color: #07292F;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 13px;
}
